import * as React from "react";
import { graphql } from "gatsby";
import { PortableText } from "@portabletext/react";
import Seo from "../../components/seo/seo.component";
import Layout from "../../components/layout/layout.component";

const PrivacyPage = ({ data }) => {
  const { _rawPrivacy: text } =
    data?.allSanitySettings?.edges?.length > 0
      ? data?.allSanitySettings?.edges[0].node
      : {};

  return (
    <Layout>
      <main className="main container-fluid has-text-left">
        <div className="mt-5">
          <PortableText
            value={text}
          />
        </div>
      </main>
    </Layout>
  );
};

export const query = graphql`
  query {
    allSanitySettings {
      edges {
        node {
          _rawPrivacy(resolveReferences: {maxDepth: 10})
        }
      }
    }
  }
`;

export default PrivacyPage;

export const Head = () => {
  return (
    <>
      <Seo pathname="privacy" />
      <body className="privacy" />
    </>
  );
};
